html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-icon1 {
  width: 0.85em;
  height: 1em;
  fill: initial;
}

.icon-icon2 {
  width: 0.88em;
  height: 1em;
  fill: initial;
}

.icon-icon3 {
  width: 0.79em;
  height: 1em;
  fill: initial;
}

body {
  font-family: "HelveticaNeue", sans-serif;
  font-size: 12px;
  line-height: 18px;
  background: url("../img/bg.jpg") repeat;
}

a {
  text-decoration: none;
}

a:hover {
  opacity: 0.7;
}

a:active {
  opacity: 0.4;
}

p {
  margin-bottom: 20px;
}

.icon-icon3 {
  fill: #ffffff;
}

.container {
  width: 940px;
  margin: 0 auto;
}

.left {
  float: left;
}

.ovr-hidd {
  overflow: hidden;
}

.content__header {
  display: flex;
  justify-content: center;
  margin-bottom: 25px;
}

.content__header-line {
  flex-grow: 1;
  min-height: 60px;
  background-color: #f3af9d;
}

.content__header-center {
  flex: none;
  margin: 0 10px;
  background-color: #e8303c;
}

.content__title {
  font-family: "Bebas", sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 400;
  padding: 14px 20px;
  color: #ffffff;
  text-transform: uppercase;
}

.site-bar {
  float: right;
  width: 220px;
}

.site-bar__item {
  margin-bottom: 35px;
}

.site-bar__title-wrapper {
  display: flex;
  width: 100%;
  margin-bottom: 15px;
}

.site-bar__h3 {
  font-size: 14px;
  line-height: 16px;
  color: #e8303c;
  margin-right: 15px;
  text-transform: uppercase;
  flex: none;
}

.site-bar__title-bg {
  flex-grow: 1;
  background: url("../img/line.png") repeat-x;
  background-position: bottom;
}

.about-jetro {
  color: #464646;
}

.categories__list-link {
  color: #464646;
}

.ph-gallery__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.ph-gallery__item {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  border: 1px solid #e8303c;
  background-color: #f5b19f;
}

.main-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
  margin: 0 10px;
}

.nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav__item {
  line-height: 20px;
  margin-left: 27px;
}

.nav__link {
  color: #b7b4b4;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}

.toggler {
  display: none;
}

.post-block {
  position: relative;
  width: 670px;
  padding-bottom: 40px;
  margin-bottom: 45px;
  background: url("../img/line.png") repeat-x;
  background-position: bottom left;
}

.post-block__icon {
  position: absolute;
  top: 0;
  left: -85px;
  width: 75px;
  height: 75px;
  font-size: 32px;
  text-align: center;
  padding-top: 20px;
  background-color: #e8303c;
}

.post-block__icon_3 {
  background: #e8303c url("../img/sprite.png") -17px -117px no-repeat;
}

.post-block__img {
  margin-bottom: 25px;
}

.post__title {
  font-size: 18px;
  line-height: 18px;
  color: #e8303c;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.post__title_with-link {
  display: flex;
  flex-direction: column;
}

.post__bg-grey {
  position: relative;
  padding: 20px 20px 20px 75px;
  font-style: italic;
  background: #f2f2f2;
}

.post__bg-grey::before {
  position: absolute;
  top: 17px;
  left: 35px;
  content: "";
  width: 30px;
  height: 30px;
  background: url("../img/sprite.png") -40px -40px no-repeat;
}

.post__link {
  font-size: 12px;
  color: #bababa;
  text-transform: none;
  margin-top: 5px;
}

.options {
  width: 140px;
}

.options__name {
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  color: #e8303c;
  margin-bottom: 10px;
}

.options__defin {
  display: flex;
  margin-bottom: 15px;
}

.options__link {
  color: #bababa;
}

.options__link_after-line {
  position: relative;
  margin-left: 16px;
}

.options__link_after-line::after {
  position: absolute;
  top: 4px;
  left: -8px;
  content: "";
  width: 1px;
  height: 10px;
  background-color: #bababa;
}

.pagination {
  display: flex;
  margin-bottom: 50px;
}

.pagination__item {
  margin-right: 4px;
  text-align: center;
}

.pagination__link {
  display: block;
  width: 26px;
  height: 26px;
  padding-top: 7px;
  color: white;
  background-color: #f3b29d;
}

.pagination__link.active {
  background-color: #e8303c;
}

@media only screen and (max-width: 1200px) {
  .container {
    width: 700px;
  }
  .post-block {
    width: 440px;
  }
  .post-block__img img {
    width: 440px;
  }
}

@media only screen and (max-width: 768px) {
  body {
    font-size: 14px;
  }
  .container {
    width: 90%;
  }
  .main-nav {
    flex-direction: column;
  }
  .logo {
    padding-top: 20px;
  }
  .nav {
    display: block;
    width: 100%;
    margin: 20px 0;
    border-top: 1px solid #dbdbdb;
  }
  .nav__item {
    margin: 0;
  }
  .nav__link {
    display: block;
    text-align: center;
    padding: 10px;
    border-bottom: 1px solid #dbdbdb;
  }
  .nav__item:last-child {
    border: none;
  }
  .nav.active {
    flex-direction: column;
  }
  .site-bar {
    display: none;
  }
  .post-block {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .post-block__img img {
    width: 100%;
  }
  .post-block__icon {
    display: none;
  }
  .post__title {
    text-align: center;
  }
  .left {
    float: none;
  }
  .options {
    display: flex;
    justify-content: space-between;
    width: 100%;
    order: 1;
  }
  .options__item {
    text-align: center;
  }
  .options__link {
    width: 100%;
  }
  .pagination {
    justify-content: center;
  }
}

.btn {
  display: none;
}
