.post-block {

	position: relative;
	width: 670px;
	padding-bottom: 40px;
	margin-bottom: 45px;
	background: url("../img/line.png")  repeat-x;
	background-position: bottom left;


	&__icon {
		position: absolute;
		top: 0;
		left: -85px;
		width: 75px;
		height: 75px;
		font-size: 32px;
		text-align: center;
		padding-top: 20px;
		background-color: $colorTheme; }

	&__icon_3 {
		background: $colorTheme url("../img/sprite.png") -17px -117px no-repeat; }

	&__img {
		margin-bottom: 25px; } }


.post {

	&__title {
		font-size: 18px;
		line-height: 18px;
		color: $colorTheme;
		text-transform: uppercase;
		margin-bottom: 20px;

		&_with-link {
			display: flex;
			flex-direction: column; } }

	&__bg-grey {
		position: relative;
		padding: 20px 20px 20px 75px;
		font-style: italic;
		background: #f2f2f2;

		&::before {
			position: absolute;
			top: 17px;
			left: 35px;

			content: "";
			width: 30px;
			height: 30px;
			background: url("../img/sprite.png") -40px -40px no-repeat; } }

	&__link {
		font-size: 12px;
		color: #bababa;
		text-transform: none;
		margin-top: 5px; } }

