.site-bar {
	float: right;
	width: 220px;

	&__item {
		margin-bottom: 35px; }

	&__title-wrapper {
		display: flex;
		width: 100%;
		margin-bottom: 15px; }

	&__h3 {
		font-size: 14px;
		line-height: 16px;
		color: $colorTheme;
		margin-right: 15px;
		text-transform: uppercase;
		flex: none; }

	&__title-bg {
		flex-grow: 1;
		background: url("../img/line.png") repeat-x;
		background-position: bottom; } }

.about-jetro {

	color: #464646; }

.categories {

	&__list-link {
		color: #464646; } }

.ph-gallery {

	&__wrapper {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }

	&__item {
		width: 50px;
		height: 50px;
		margin-bottom: 5px;
		border: 1px solid $colorTheme;
		background-color: #f5b19f; } }
